<template>
	<!-- ok -->
	<view class="wanlpage-banner" :style="[pageData.style]">
		<swiper class="screen-swiper square-dot" :style="{height: pageData.params.height}" :indicator-dots="true" :circular="true" :autoplay="true" :interval="pageData.params.interval" duration="500">
			<swiper-item v-for="(image, keys) in pageData.data" :key="keys" @tap="$flbooth.on(image.link)">
				<image :src="$flbooth.oss(image.image, 414, 0, 1, 'transparent', 'png')" mode="aspectFill"></image>
			</swiper-item>
		</swiper>
	</view>
</template>
<script>
	export default {
		name: "WanlPageBanner",
		props: {
			pageData: {
				type: Object,
				default: function() {
					return {
						name: '',
						type: '',
						params: [],
						style: [],
						data: []
					}
				}
			}
		}
	}
</script>
<style lang="scss">
	.screen-swiper{
		min-height: 100rpx !important;
		image{
			overflow: hidden;
		}
	}
</style>
